import BaseApiService from "services/BaseApiService";
const DEFAULT_PATH = "/custom/api/financial/detail";

class FinancialService extends BaseApiService {
  getAll = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getAllCorporateHistorical = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };

  getAllCorporateInvestment = (start = 0, limit = 10, search: string) => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
}

export default new FinancialService();
